.version_list {
  margin: 0;
  padding: 0;
  margin-top: 10px;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 80%;
}
.version_list__item {
  display: flex;
  flex-direction: column;
  font-size: 12px;
  width: 100%;
  margin-bottom: 5px;
  padding: 5px;
  border-bottom: 1px solid black;
}
